body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: radial-gradient(#d3d3d3,#747474, #353535);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.all{
  width: 409px;
  height: 500px;
  /* background-color: #222222; */
  overflow-y: auto;
  overflow-X: hidden;
  position: relative;
  border-radius: 10px;
  box-shadow: 0 0 11px black;
}
.list{
  width: 400px;
  height: 500px;
  position: relative;
  margin-top: -10px;
}
.item{
  width: 100%;
  position: absolute;
  left: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
  height: 50px;
  /* background-color: rgba(0, 0, 0, 0); */
  /* background-color: rgb(212, 66, 9); */
}
.side{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.center{
  background-color: white;
  width: 100%;
  height: 90%;
}
.edit{
  width: 300px;
  height: 100px;
  background-color: rgb(144, 206, 81);
  position: absolute; 
  top: 50%;
  left: 50%;
  display: none;
}
.button{
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
  background-color: rgba(137, 43, 226, 0);
}
.button:hover{
  cursor: pointer;
}
.button:disabled{
  opacity: 0;
}
.add{
  width: 400px;
  height: 70px;
  background-color: rgb(0, 0, 0);
  position: absolute;
  bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}


.all::-webkit-scrollbar {
  width: 9px;
}
/* Track */
::-webkit-scrollbar-track {
  background: white; 
}
/* Handle */
::-webkit-scrollbar-thumb {
border-radius: 10px;
  background: rgb(39, 39, 39); 
  box-shadow: inset 0 0 5px rgba(255, 255, 255, 0.459); 
  margin-right: 11px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(59, 59, 59); 
  box-shadow:  0 0 5px black; 
  border-radius: 10px;
}
